body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

main > :first-child {
  margin-top: 0;
}

main > :last-child {
  margin-bottom: 0;
}
@tailwind base;
@tailwind components;
@tailwind utilities;

.cytoscape-react-nodes-and-edges {
  position: absolute;
  z-index: 10;
}

.cytoscape-react-cy-container {
  width: 100%;
  height: 1000px;
}

table td, th {
  border: 1px solid black;
  font-size: large;
  font-style: normal;

}
table tr {
border: 1px solid black;
font-size: x-small;
font-style: italic;

}

.rp-node-a {
  background-color: #7070e0;
}

.rp-node-b {
  background-color: #e070e0;
}

.rp-node-c {
  background-color: #808080;
}

.rp-randomwords {
  background-color: #e09b70;
  width: 120px;
  height: 45px;
  
}
