.dragable{  
  width: 100%;
  background-color: #c5c3be;
  margin-top: auto;
  display: flex;
  align-items: center;  
}
.dragContainer{
  /* background-color: #A569BD ; */
  margin-inline: 10px;
}
.row{
  display: flex;
  flex-direction: row;
  padding: 5px;
  background-color: white ;
  margin-block: 7px;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  width: 75%;
  margin-inline: 10px;
  border-radius: 50px;
  margin-top:10px,
}
.subContainer{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
  grid-auto-rows: 100px;
  grid-template-areas:
    "a b"
    "a b"
    "c c"
    "c c"
    "c c"
    "c c";   
   
  align-items: start;  
  width: 100%;  
}
.item1 {
  grid-area: a;
  background-color: #c8dbe4;
  width: 100%;
  height: 100%;  
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  position: relative;
  padding: 14px;
  border-radius: 25px;
  overflow: scroll;
}
.item2 {
  grid-area: b;
  background-color: #c8dbe4;  
  width: 100%;
  height: 100%;  
  text-align: start;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;  
  padding: 14px;
  border-radius: 25px; 
  overflow-y: scroll;
}

.item2::-webkit-scrollbar , .item1::-webkit-scrollbar {
 width: 5px;
 height: 8px; 

}

.item3 {
  grid-area: c;
  background-color: #c8dbe4;
 width: 100%;
 height: 100%;
 overflow-y: auto;
 display: grid;
 grid-auto-rows: 70px;
 grid-template-columns: repeat(5, 1fr);
 gap: 10px;
 border-radius: 25px;
 border-color: black;
 position: relative;
 box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px; 
 padding-block: 27px;
}
.item3 > div{
  background-color: #52ab98;
  padding: 5px;
  border-radius: 10px;
  height: 49px;
  display: flex;
  flex-direction: column;
  border-color: black;
  width: 205px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  justify-content: center;


}
.drag{
  margin-inline: 5px;
  font-size: large;
}
.row:hover {
 background-color: #52ab98;
  cursor: grab;
}

.row:active {
  cursor: grabbing;
}
.drag-enter {
  border: 2px dashed gray;
}
.buttonStyle{
  text-align: "center";
  cursor: "pointer";
  position: "absolute";
  /* right: 15, */
  top: 20;
  background-color: "bisque";
  border: "none";
  width: "100px";
  border-radius: 10;
  padding: "10px";
  font-size: "large";
}
.add{
  right: 15,

}
.create{
  right: 150,

}